.quantity {
    flex: 1;
    position: relative;
    height: 6rem;
    overflow: hidden;
}

.quantity>main {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 1rem;
}

.quantity>main .text-field,
.quantity>main button {
    z-index: 5;
    height: 3rem;
}

.quantity>footer label  {
    min-width: 64px;
    text-align: center;
    font-size: .8rem;
    font-style: italic;
    color: var(--secondary-text-color);
}

.quantity>footer {
    position: absolute;
    top: 35px;
    width: 100%;
    display: flex;
    align-items: center;
    height: 5rem;
}