picture.avatar {
  max-height: 4rem;
}

.avatar>div {
  width: 4rem;
  height: 4rem;
  overflow: hidden;
  border-radius: 4rem;
  background-color: rgba(200,200,200,.5);
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: capitalize;
  font-size: 1.4rem;
  font-weight: 600;
}

.avatar>img {
  width: 4rem;
  height: 4rem;
  overflow: hidden;
  border-radius: 4rem;
}

.thumb {
  width: 5.6rem;
  height: 5.6rem;
  overflow: hidden;
  background-color: rgba(240,240,240,.5);
  object-fit: cover;
}

.thumb.alt {
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgba(100,100,100,.5);
  font-size: 1.2rem;
  font-weight: 500;
}

.picture {
  flex: 1;
  position: relative;
  overflow: hidden;
  background-color: rgba(240,240,240,.5);
}

.picture>img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.picture.alternative {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Roboto Condensed';
  font-weight: 600;
  color: rgba(150,150,150,.8);
}

img.error:after {
  content: attr(alt);

  font-size: 1.2rem;
  font-weight: 600;

  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  background-color: #f5f5f5;

  display: flex;
  justify-content: center;
  align-items: center;
}
