.header {
  display: flex;
  padding: 1rem;
  align-items: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.header>.icon .header>img {
  margin-right: 1.6rem
}

.header>label {
  margin-left: 1rem
}

.header.caption {
  height: 4rem;
  border-bottom: solid 1px var(--divider-color);
}

.header.dense {
  height: 4.8rem;
}

.header.prominent {
  height: 12.8rem;
  align-items: flex-start;
}

.header.prominent>label {
  align-self: flex-end;
}

.header-icon {
  max-width: 4rem;
  max-height: 4rem;
  object-fit: contain;
  padding: .5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header>.actions {
  position: relative;
  flex: 1;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  overflow: auto;
}

.header.primary {
  font-size: 2rem;
  font-weight: 500;
  background-color: var(--primary-color-dark);
  color: var(--primary-color-text);
}

.header.secondary {
  color: var(--secondary-color-text);
  background-color: var(--secondary-color-dark);
}
