
.card {
  min-width: 16rem;
  min-height: 16rem;
  margin: .2rem;
  padding: 0;
  background-color: var(--paper-color);
  border: solid 1px rgba(100,100,100,.1);
  border-radius: 3px;
  box-shadow: 1px 6px 8px -1px rgba(180,180,240,.3),
                0 -1px 8px 5px hsla(0,0%,94%,.3);
  transition: all .3s;

  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.card:hover {
  cursor: pointer;
  transform: translateY(-2px);
}

.card>section.hover {
  position: absolute;
  width: 0px;
  height: 0px;
  color: rgba(220,220,220,.9);
  background-color: rgba(0,0,0,.85);
  opacity: 0;
  transition: opacity .5s linear;

  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.card:hover>section.hover {
  width: 100%;
  height: calc(100% - 3rem);
  padding: 1rem;
  opacity: .8;
  transition: opacity .5s linear;
  text-transform: uppercase;
  font-weight: 500;
}

.card>picture {
  flex: 1;
  overflow: hidden;
}

.card>picture>img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card>footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.card>footer>header {
  height: 2rem;
  line-height: 2rem;
  font-size: 1em;
  font-weight: 500;
  margin: .5rem;
  color: rgba(0,0,0,.8);
  text-transform: uppercase;
}
