.tree {
    padding: .5rem 0;
}

.tree-node {
    margin: 0;
    padding: 0;
    padding-left: 1rem;
}

.tree-item{
    margin: 0;
    padding: .5rem 0;
    display: flex;
    align-items: center;
    font: 1rem;
    color: rgba(0,0,0,.7);
}

.tree-item:hover {
    cursor: pointer;
    background-color: rgba(200,200,200,.7);
    font-weight: 500;
} 

.tree-item.final {
    margin-left: 1rem;
    padding-left: 1rem;
}

.tree-item>i {
    padding-left: .3rem;
}

.tree-item>span {
    padding-left: .8rem;
}

.tree-node>summary::before {
    content: "\25B6";
    color: black;
    display: inline-block;
    margin-right: 6px;
    font-size: 10px;
    color: rgba(0,0,0,.3);
}

details[open].tree-node>summary::before {
	transform: rotate(90deg)
}