.notifications {
  position: absolute;
  right: -30rem;
  top: 6rem;
  width: 30rem;
  transition: right 1s;
  z-index: 10;
}

.notifications.open {
  right: 0.1px;
}

.notification-card {
  position: relative;
  display: flex;
  align-items: center;
  border-radius: 1rem !important;
  animation: buzz .5s ease-in-out 1;
  background-color: rgba(255,255,255, .8);
}

.notification-card:first-child {

}

.notification-card h1 {
  font-size: 1rem;
  font-weight: 400;
}

.notification-card h2 {
  font-size: 1.1rem;
  font-weight: 400;
}

.closer {
  position: absolute;
  top: .5rem;
  right: .5rem;
  font-size: .8rem;
  width: 3rem;
  height: 3rem;
}

.activity-token {
  width: 10rem;
  height: 10rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem;
}

.activity-token h1 {
  color: rgba(0,0,0,.7);
  font-weight: 400;
  font-size: 1.2rem;
}

.activity-token h2 {
  color: rgba(0,0,0,.7);
  font-weight: 200;
  font-size: 1rem;
}

@keyframes buzz {
  0%{
    transform: rotate(-7deg);
  }

  25%{
    transform: rotate(5deg);
  }

  50%{
    transform: rotate(-3deg);
  }

  75%{
    transform: rotate(1deg);
  }

  100%{
    transform: rotate(0deg);
  }
}
