fieldset {
  border: solid 1px var(--divider-color);
  margin: 1rem 0;
}

.field {
  padding: 1rem;
}

.field .label-box {
  padding-left: .6rem;
  padding-bottom: .2rem;
  font-weight: 400;
  color: var(--secondary-text-color);
  text-transform: capitalize;
}

.field .field-box {
  display: flex;
  align-items: center;
  border: solid 1px var(--divider-color);
  padding: .2rem;
  background-color: var(--paper-color);
}

.field .field-box:focus-within {
  border-color: var(--primary-color);
  box-shadow: var(--shadow1);
}

.field .field-box.readOnly {
  border: 0;
  border-bottom: solid 1px var(--divider-color);
}

.field .field-box .icon-box{
  border-right: solid 1px var(--divider-color);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 3.6rem;
  color: var(--secondary-text-color)
}

.field .field-box input,
.field .field-box select,
.field .field-box textarea {
  height: 4rem;
  border: 0;
  flex: 1;
  align-self: stretch;
  margin: 0 .3rem;
  font-size: 1.4rem;
  font-weight: 500;
  background-color: transparent;
  color: var(--primary-text-color);
}

.field .info-box {
  text-align: right;
  padding-top: .2rem;
  padding-right: .6rem;

}

.field .checkfield {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-right: 1rem;
}