.color-item {
    height: 3rem;
    width: 3rem;
    border-radius: 2rem;
    margin: .5rem;
    cursor: pointer;
    border: solid 1px var(--divider-color);
}

.color-item.min {
    width: 2rem;
    height: 2rem;
}

.color-item.empty {
    border: dashed 2px var(--divider-color);
}

.color-list {
    display: flex;
    align-items: center;
}

.color-list button {
    font-size: 1rem;
}

.color-item-custom {
    border: solid 1px var(--divider-color);
    height: 3rem;
    min-width: 3rem;
    padding: .3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: .8rem;
}