.timeline {
    border: solid 1px var(--divider-color);
    border-radius: 5px;
    width: 100%;
    background-color: #EEE;
    display: grid;
    grid-gap: 1px;
    overflow: auto;
}

.timeline>header {
    color: #999;
    z-index: 10;
    background-color: #FFF;
    min-height: 40px;
}

.timeline>header.aside {
    position: sticky;
    left: 0px;
    z-index: 11;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 0 1rem;
    border-right: solid 1px var(--divider-color);
}

.timeline>header.column {
    position: sticky;
    left: 200px;
} 

.timeline .cell {
    background-color: #FFF;
    min-height: 40px;
}

.timeline .empty-cell {
    background-color: #FFF;
}

.timeline .task {
    border: solid 1px var(--divider-color);
    border-radius: 3px;
    background-color: #FFF;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 9;
    margin: 5px;
}