.menu-icon {
  position: relative;
  overflow: visible;
}

.menu-box {
  position: absolute;
  right: .5rem;
  min-width: 10rem;
  min-height: 1rem;
  z-index: 100;

  display: block;
  box-shadow: var(--shadow1);
  background-color: var(--paper-color);
  color: var(--primary-text-color);
  padding: .8rem 0;
}

.menu-item {
  display: flex;
  align-items: center;
  padding: .5rem 1rem;
  white-space: nowrap;
}

.menu-item.disabled {
  display: flex;
  align-items: center;
  padding: .5rem;
}

.menu-item>label {
  color: var(--primary-text-color);
  font-size: 1.4rem;
  font-weight: 500;
}

.menu-item:not(.disabled):hover {
  background-color: var(--hover-bgcolor);
  cursor: pointer;
}
