.page6 {
    height: 100%;
    --page-menu-width: 10rem;
    --page-menu-color: #111;
    --page-menu-bgcolor: #EEE;
    --page-header-height: 4rem;
    --page-nav-height: 3rem;
    --page-aside-width: 10rem;
    --page-aside-color: #111;
    --page-aside-bgcolor: #EEE;
    --page-footer-height: 3rem;
}

.page6.paper {
    background-color: var(--paper-color);
    border: solid 1px var(--divider-color);
    margin: 20px;
}

.page6>menu {
    padding: 0px;
    margin: 0px;
    grid-area: menu;
    width: var(--page-menu-width);
    color: var(--page-menu-color);
    background-color: var(--page-menu-bgcolor);
}

.page6>header {
    grid-area: header;
    border-bottom: dotted 1px var(--divider-color);
    height: var(--page-header-height);
    display: flex;
    align-items: center;
}

.page6>nav {
    grid-area: nav;
    border-bottom: dotted 1px var(--divider-color);
    height: var(--page-nav-height);
    display: flex;
}

.page6>main {
    grid-area: main;
}

.page6>aside {
    grid-area: aside;
    min-width: var(--page-aside-width);
    color: var(--page-aside-color);
    background-color: var(--page-aside-bgcolor);
}

.page6>footer {
    grid-area: footer;
    border-top: dotted 1px var(--divider-color);
    min-height: var(--page-footer-height);
}

.page6>details {
    position: absolute;
    bottom: 0px;
    left: 0px;
    right: 0px;
    margin: auto;
    min-width: 30rem;
    width: 100vw;
    max-width: 50rem;
    min-height: 3rem;
    background-color: var(--paper-color);
    border-radius: 5px 5px 0 0;
    overflow: hidden;
    box-shadow: var(--shadow-top);
}

.page6>details>summary {
    display: flex;
    align-items: center;
    min-height: 3rem;
}

summary {
    /* list-style-type: none; /* Firefox */
    padding: 0 0 0 1rem;
}
  
summary::-webkit-details-marker {
    display: none; /* Chrome */
}

details[open] main {
	animation-name: invisiblyGrowFontSize, fadeIn;
    animation-duration: 500ms, 200ms;
    animation-delay: 0ms, 500ms;
}
  
/* add wrapper, continue with font-size */
@keyframes invisiblyGrowFontSize {
	0% {
        font-size: 0;
        opacity: 0;
	}
	100% {
        font-size: 1em;
        opacity: 0;
	}
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
	100% {
		opacity: 1;
	}
}