.wizard {
    box-shadow: var(--shadow1);
    margin: 2rem;
}

.wizard-navigation {
    height: 4rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-shadow: var(--shadow-top);
    padding: 0 1rem;
}

.wizard-navigation>.dots {
    display: flex;
    align-items: center;
    justify-content: center;
}

.dots>.dot {
    height: 1rem;
    width: 1rem;
    border-radius: 1rem;
    border: solid 2px var(--divider-color);
    background-color: var(--paper-color);
    margin: 0 .5rem;
}

.dots>.dot.selected {
    background-color: var(--divider-color);
}

.wizard-breadcrumb-bar {
    height: 3rem;
    padding: 1rem;
    display: flex;
    align-items: center;
}

.wizard-breadcrumb-bar>.breadcrumb {
    font-size: 1rem;
    color: var(--divider-color);
    text-transform: capitalize;
}

.wizard-breadcrumb-bar>.breadcrumb.selected {
    color: black;
    font-weight: 500;
}

.wizard-breadcrumb-bar>.breadcrumb>.divider {
    padding: 0 1rem;
    color: red;
}

.wizard-breadcrumb-bar>.breadcrumb:last-of-type>.divider {
    display: none;
}