.chip {
    padding: 0 2px;
    --chip-selection-color: rgb(64, 64, 175);
    --chip-selection-background: #ece1fd;
}

.chip.outlined>.mdc-chip {
    background-color: transparent;
    border: solid 1px #DDD;
}

.chipset {
    display: flex;
}

.chip>.mdc-chip--selected {
    color: var(--chip-selection-color) !important;
    background-color: var(--chip-selection-background) !important;
}