.tabs {
  display: flex;
  overflow-x: auto;
}

.tabs.vertical {
  flex-direction: column;
  min-width: 5rem !important;
  padding-top: 2rem;
}

.tabs.vertical .tab {
  min-width: 5rem !important;
  max-width: 5rem !important;
  max-height: 5rem;
}

.tabs.primary {
  color: var(--primary-color-text);
  background-color: var(--primary-color-dark);
}

.tabs.secondary {
  color: var(--secondary-color-text);
  background-color: var(--secondary-color-dark);
}

.tab {
  text-transform: uppercase;
  min-width: 10rem !important;
  padding: 0 1rem;
  flex: 1;
  text-align: center;
  line-height: 5rem;
  font-size: 1.4rem;
  vertical-align: middle;
  overflow: hidden;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.tab.small {
  min-width: 1rem !important;
  font-size: 1rem;
}

.tab>.badge {
  margin: 1rem;
  padding: .3rem .6rem;
  border-radius: 1rem;
}

.tab.selected {
  border-bottom: solid 2px var(--accent-color);
}

.tab.selected>.badge {
  font-weight: bold;
}

.stack {
  flex: 1;
  overflow: auto !important;
  display: flex;
  flex-direction: column;
}
