.table {
  flex: 1;
  position: relative;
  display: flex;
  flex-direction: column;
  overflow: auto;
}

.table>.table-header {
  height: 6.4rem;
  font-size: 2rem;
}

.table table {
  flex: 1;
  width: 100%;
  border-collapse: collapse;
}

.table th {
  position: sticky;
  top: 0;
  z-index: 100;
  height: 4rem;
  color: var(--text-color-light);
  text-align: left;
  font-weight: 500;
  border-bottom: solid 2px var(--divider-color-light);
  background-color: var(--paper-color);
  box-shadow: 0px 2px 0px 0px rgba(135,113,135,0.12);
}

.table th.col_title:hover {
  color: var(--text-color);
}

.table th.col_value {
  text-align: right;
}

.table tr {
  height: 4.8rem;
  overflow: hidden;
}

.table tr:hover {
  cursor: pointer;
  background-color: rgba(200,200,200,.1);
}

.table tfoot {
  height: 5.6rem;
}

.table td {
  color: var(--text-color-light);
  text-align: left;
  padding-right: 2.4rem;
  border-bottom: solid 1px var(--divider-color-light);
}

.table td.col_index {
  width: 2rem;
  padding-right: 0;
  text-align: right;
}

.table td.col_check {
  padding-right: 0;
  width: 3rem;
}

.table td.col_icon {
  padding-right: 0;
  width: 4rem;
}

.table td.col_avatar {
  min-width: 6.4rem;
  width: 6.4rem;
  height: 4rem;
}

.table td.col_title {
  text-align: left;
  color: var(--text-color-light);
  font-weight: 600;
  width: 100%;
}

.table td.col_name {
  text-align: left;
  color: var(--text-color-light);
  font-weight: 600;
}

td.col_value {
  text-align: right;
  color: var(--text-color-light);
  font-weight: 600;
}

td.col_menu {
  min-width: 4rem;
  width: 4rem;
  padding-right: 0;
}

.cell .field {
  padding: 0px;
}

.cell input {
  width: 100% !important;
  height: 2.6rem !important;
}

.cell img {
  min-width: 5rem;
  max-height: 5rem;
  object-fit: cover !important;
}

.table tr.black {
  border-left: solid 3px black;
}

.table tr.red {
  border-left: solid 3px red;
}

.table tr.orange {
  border-left: solid 3px orange;
}

.table tr.yellow {
  border-left: solid 3px yellow;
}

.table tr.blue {
  border-left: solid 3px blue;
}

.table tr.green {
  border-left: solid 3px green;
}
