.backdrop {
    background-color: var(--background-color);
    height: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
}

.backdrop>details {
    min-height: 4rem;
    overflow: auto;
}

.backdrop>details[open] {
    flex: 1;
}

.backdrop>details>summary {
    background-color: var(--background-color);
    position: sticky;
    top: 0px;
    z-index: 5;
    min-height: 4rem;
}

.backdrop>details>main {
    padding: 0 1rem;
}

.backdrop>article {
    flex: 1;
    min-height: 4rem;
    background-color: var(--paper-color);
    border-radius: 20px 20px 0 0;
    border-top: solid 1px var(--divider-color);
    display: flex;
    flex-direction: column;
}

.backdrop>article>header {
    padding: 1.4rem 2rem 1rem 2rem;
}

.backdrop>article>main {
    flex: 1;
    overflow: auto;
}