.upload-area {
  margin: 1rem;
  border: dashed 2px var(--divider-color);
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 1rem;
}

.upload-area>label {
  font-size: 1.4rem;
  font-weight: 500;
}

.upload-area.drag-over {
  background-color: rgba(200,200,200,.1);
}
