.uploader {
    display: flex;
}

.demo-uploader {
    width: 40rem;
    height: 30rem;
    margin: 5rem auto;
    flex-direction: column;
}

/***************** Upload Area ***********************/

.upload-area6 {
    flex: 1;
    margin: 0 0 1rem 0;
    border: dashed 2px var(--divider-color);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 1rem;
  }
  
  .upload-area6>label {
    font-size: 1.3rem;
    font-weight: 500;
  }
  
  .upload-area6.drag-over {
    background-color: rgba(200,200,200,.1);
  }

  /***************** Upload File ***********************/

.upload-file {
    display: flex;
    align-items: center;
    min-height: 4rem;
    padding: 0 1rem;
}

.upload-file>label {
    padding: 0 1rem;
    min-width: 40%;
    flex: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}